    font-size: 20px;\n    line-height: 120%;\n    letter-spacing: 0.01em;\n  }\n\n  @media (max-width: $max-mobile-width) {\n    font-size: 28px;\n    line-height: 32px;\n  }\n}\n","html {\n  height: 100%;\n  scroll-snap-type: y mandatory;\n}\n\n.page {\n  min-height: 100%;\n  margin: 0;\n  font-family: \"Poppins\", sans-serif;\n  font-style: normal;\n  font-weight: 400;\n  background: $main-bg-color;\n  color: $main-text-color;\n  display: grid;\n  grid-template-rows: min-content 1fr min-content;\n\n  &--opened-mobile-menu::before {\n    @include defaul-pseudo-element(100vw, 100%);\n    position: fixed;\n    z-index: 1;\n    top: 0;\n    left: 0;\n    animation-name: mobile-menu-opened;\n    animation-duration: 2s;\n    animation-fill-mode: forwards;\n  }\n\n  &--closed-mobile-menu::before {\n    @include defaul-pseudo-element(100vw, 100vh);\n    z-index: 0;\n    top: 0;\n    left: 0;\n    animation-name: mobile-menu-closed;\n    animation-duration: 2s;\n    animation-fill-mode: forwards;\n  }\n\n  @keyframes mobile-menu-opened {\n    from {\n      background: rgba(0, 0, 0, 0);\n      backdrop-filter: blur(0px);\n    }\n    to {\n      background: rgba(0, 0, 0, 0.05);\n      backdrop-filter: blur(13px);\n    }\n  }\n  @keyframes mobile-menu-closed {\n    from {\n      background: rgba(0, 0, 0, 0.05);\n      backdrop-filter: blur(13px);\n    }\n    to {\n      background: none;\n      backdrop-filter: blur(0px);\n    }\n  }\n}\n",".button {\n  font-family: \"Poppins\", sans-serif;\n  font-weight: 600;\n  font-size: 16px;\n  line-height: 24px;\n  @include button-none();\n  color: #ffffff;\n  padding: 12px 42px;\n  cursor: pointer;\n  transition: 0.45s;\n\n  @media (max-width: $max-mobile-width) {\n    padding: 6px 16px;\n    font-size: 14px;\n    line-height: 21px;\n  }\n\n  &--without-bg {\n    background: rgba(255, 255, 255, 0.01);\n    box-shadow: 0px 4px 0px #d57709;\n    border: 2px solid #f7de0d;\n    border-radius: 49px;\n\n    &:hover {\n      box-shadow: none;\n    }\n\n    &:active {\n      background: linear-gradient(180deg, #f7de0d 3.5%, #f7a50d 100%),\n        rgba(255, 255, 255, 0.01);\n      box-shadow: 0px 4px 0px #d57709;\n    }\n\n    &:disabled {\n      background: linear-gradient(180deg, #f7de0d 3.5%, #f7a50d 100%),\n        rgba(255, 255, 255, 0.01);\n      opacity: 0.6;\n    }\n  }\n\n  &--with-bg {\n    background: linear-gradient(180deg, #f7de0d 3.5%, #f7a50d 100%),\n      rgba(255, 255, 255, 0.01);\n    box-shadow: 0px 4px 0px #d57709;\n    border-radius: 49px;\n\n    &:hover {\n      box-shadow: none;\n    }\n\n    &:active {\n      box-shadow: inset -4px -4px 4px rgba(255, 255, 255, 0.45),\n        inset 4px 4px 4px rgba(11, 9, 0, 0.35);\n    }\n  }\n}\n",".text {\n  font-size: 16px;\n  line-height: 145%;\n  opacity: 0.8;\n\n  &--white {\n    color: #ffffff;\n    opacity: 1;\n  }\n}\n",".img-container {\n  z-index: 5;\n  height: 27vw;\n  display: flex;\n  align-items: center;\n  transition: 0.7s;\n  background: url(\"./../../public/img/horse-bg.png\") no-repeat center center;\n  background-size: contain;\n  justify-content: center;\n\n  &:hover {\n    background-image: url(\"./../../public/img/horse-bg--hover.png\");\n  }\n\n  @media (max-width: $max-mobile-width) {\n    height: 57vw;\n    margin-bottom: 32px;\n    width: 100%;\n  }\n\n  @media (min-width: 1440px) {\n    height: 400px;\n  }\n}\n",".block {\n  z-index: 8;\n  padding-bottom: 160px;\n\n  @media (max-width: $max-mobile-width) {\n    padding-bottom: 120px;\n  }\n}\n",".cloud-container {\n  position: relative;\n  height: 0;\n  width: 100vw;\n\n  @media (max-width: $max-mobile-width) {\n    display: none;\n  }\n}\n\n.cloud {\n  position: absolute;\n  z-index: 0;\n  animation: cloud-animation--right-to-left 8s infinite both linear;\n\n  &--left-to-right {\n    animation: cloud-animation--left-to-right 8s infinite both linear;\n\n    &-speed {\n      animation-duration: 8s;\n    }\n  }\n\n  &--right-to-left {\n    animation: cloud-animation--right-to-left 9s infinite both linear;\n  }\n\n  &--litle {\n    animation-duration: 12s;\n  }\n\n  &--1 {\n    top: 55px;\n  }\n\n  &--2 {\n    top: 105px;\n\n    animation: cloud-animation--left-to-right 10s infinite both linear;\n  }\n\n  &--3 {\n    top: -125px;\n  }\n\n  &--4 {\n    top: 105px;\n    animation: cloud-animation--left-to-right 10s infinite both linear;\n  }\n\n  &--5 {\n    top: -105px;\n  }\n\n  &--6 {\n    top: -805px;\n  }\n\n  &--7 {\n    top: -705px;\n    animation: cloud-animation--left-to-right 10s infinite both linear;\n  }\n\n  &--8 {\n    top: 50px;\n  }\n\n  &--9 {\n    top: -135px;\n  }\n\n  &--10 {\n    top: -130px;\n  }\n\n  &--11 {\n    top: 0px;\n  }\n\n  &--12 {\n    top: -100px;\n  }\n\n  &--13 {\n    top: -50px;\n  }\n}\n\n.block-animation {\n  @media (max-width: $max-mobile-width) {\n    opacity: 0;\n    transition: 1.75s;\n\n    &--active {\n      opacity: 1;\n    }\n  }\n}\n\n@keyframes cloud-animation {\n  from {\n    left: -650px;\n  }\n  to {\n    left: 110vw;\n  }\n}\n\n@keyframes cloud-animation--left-to-right {\n  from {\n    left: -650px;\n  }\n  to {\n    left: 110vw;\n  }\n}\n\n@keyframes cloud-animation--right-to-left {\n  from {\n    right: -650px;\n  }\n  to {\n    right: 110vw;\n  }\n}\n",".main-header {\n  padding-top: 32px;\n  padding-bottom: 63.26vw;\n\n  @media (max-width: $max-mobile-width) {\n    padding-top: 12px;\n    padding-bottom: 0;\n  }\n\n  & .main-header__navigation {\n    position: fixed;\n    z-index: -9;\n    width: calc(100vw - 64px);\n    margin: 0 auto;\n\n    @media (max-width: $max-mobile-width) {\n      position: relative;\n      z-index: 6;\n    }\n  }\n}\n\n.main-header__mobile-menu-button {\n  display: none;\n  position: absolute;\n  right: 0;\n}\n\n.main-header__mobile-menu-container {\n  width: 29%;\n}\n",".navigation {\n  @include list-none();\n  display: flex;\n  flex-wrap: wrap;\n  width: 100%;\n  align-items: center;\n  justify-content: space-between;\n  position: relative;\n\n  @media (max-width: $max-mobile-width) {\n    width: calc(100vw - 32px);\n    z-index: 5;\n  }\n}\n\n.navigation__list {\n  @include list-none();\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: space-between;\n\n  &--mobile-buttons {\n    @include list-none();\n    margin-top: 40px !important;\n  }\n\n  @media (min-width: $max-mobile-width) {\n    &--mobile-buttons {\n      display: none;\n    }\n  }\n}\n\n.navigation__link-item {\n  &--whitepaper {\n    position: relative;\n\n    &:hover .language-settings {\n      border: 2px solid #f8ab0e;\n      padding: 12px 16px;\n      height: 100px;\n      transition: 0.5s;\n      transition-delay: 0;\n    }\n\n    &:hover .language-settings__button {\n      font-size: 16px;\n      line-height: 24px;\n      transition: 1s;\n      transition-delay: 0.4s;\n    }\n  }\n\n  &--mobile-buttons {\n    margin-top: 24px;\n  }\n}\n\n.navigation__link {\n  font-weight: 600;\n  font-size: 16px;\n  line-height: 24px;\n\n  color: #ffffff;\n  text-decoration: none;\n\n  &:hover {\n    color: #f8ab0e;\n  }\n\n  &:active {\n    color: #f8ab0e;\n  }\n\n  &:disabled {\n    color: #f8ab0e;\n  }\n\n  &:focus {\n    text-transform: uppercase;\n    color: #ffffff;\n    text-shadow: 0px 2px 0px #d57709;\n  }\n\n  &--logo {\n    @media (max-width: $max-mobile-width) {\n      margin-right: 25%;\n    }\n\n    @media (max-width: 355px) {\n      margin-right: 15%;\n    }\n  }\n}\n\n@media (max-width: $max-mobile-width) {\n  .main-header__mobile-menu-button {\n    display: block;\n    @include button-none();\n    width: 33px;\n    height: 32px;\n    background: url(\"./../../public/img/heroicons-solid_menu-alt-1.svg\")\n      no-repeat center center;\n    cursor: pointer;\n    z-index: 3;\n    position: fixed;\n    right: 16px;\n\n    &--opened {\n      transition: 1.5s;\n      transform: rotate(360deg);\n      background: url(\"./../../public/img/raphael_cross.svg\") no-repeat center\n        center;\n    }\n\n    &--closed-animation {\n      transition: 2s;\n      transform: rotate(-360deg);\n    }\n  }\n\n  .navigation__mobile-button {\n    padding: 0;\n    font-size: 0;\n    line-height: 0;\n    box-shadow: none;\n    border: none;\n    transition: 2s;\n  }\n\n  .navigation__button {\n    margin-right: auto;\n  }\n\n  .main-header__mobile-menu-container {\n    left: 0;\n    width: 100vw;\n    z-index: 2;\n    left: 0px;\n    text-align: left;\n    top: -100vh;\n    background: url(\"./../../public/img/mobile-menu-bg.png\") no-repeat center\n      top;\n    background-size: cover;\n    height: 0;\n    transition: background-size 1s;\n    transition-delay: 3s;\n    position: fixed;\n\n    &--opened {\n      animation: mobile-menu__appearance 2s;\n      animation-fill-mode: forwards;\n      padding-top: 100px;\n      background-size: cover;\n\n      .navigation__link {\n        font-size: 18px;\n        line-height: 27px;\n      }\n\n      .navigation__mobile-button {\n        padding: 16px 0;\n        font-size: 16px;\n        line-height: 24px;\n        box-shadow: 0px 4px 0px #d57709;\n        border: 2px solid #f7a50d;\n      }\n    }\n    &--closed {\n      animation: mobile-menu__disappearing 2s;\n      animation-fill-mode: forwards;\n    }\n\n    .main-header__mobile-menu-container-centring {\n      @include wrapper(16px, 100wv);\n      position: relative;\n    }\n  }\n\n  .main-menu {\n    margin-bottom: 150px;\n  }\n\n  .navigation__list {\n    flex-direction: column;\n    text-align: center;\n  }\n\n  .navigation__link-item {\n    &:not(:last-child) {\n      margin-bottom: 32px;\n    }\n  }\n\n  .navigation__link {\n    font-size: 0;\n    line-height: 0;\n    transition: 2s;\n  }\n\n  .navigation__mobile-button {\n    display: block;\n    width: 100%;\n  }\n\n  @keyframes mobile-menu__appearance {\n    from {\n      top: -100vh;\n      height: 0;\n    }\n    to {\n      top: 0px;\n      height: 600px;\n    }\n  }\n  @keyframes mobile-menu__disappearing {\n    from {\n      top: 0px;\n      height: 600px;\n    }\n    to {\n      top: -100vh;\n      height: 0;\n    }\n  }\n}\n",".language-settings {\n  position: absolute;\n  @include list-none();\n  background: #3a426b;\n  box-shadow: 0px 6px 0px #d57709;\n  border-radius: 24px;\n  min-width: 137px;\n  height: 0;\n  transition: 1s;\n  transition-delay: 0.4s;\n  top: 32px;\n  z-index: 1;\n}\n\n.language-settings__lang {\n  &:not(:last-child) {\n    margin-bottom: 12px;\n  }\n}\n\n.language-settings__button {\n  @include button-none();\n  font-family: \"Poppins\";\n  font-style: normal;\n  font-weight: 600;\n  font-size: 0;\n  line-height: 0;\n  transition: 0.3s;\n  transition-delay: 0s;\n\n  color: #ffffff;\n  cursor: pointer;\n  position: relative;\n\n  &:hover {\n    &::before {\n      animation: hover-animation 0.55s forwards;\n      @include defaul-pseudo-element(0%, 2px);\n      bottom: 0px;\n      background-color: #ffffff;\n    }\n  }\n}\n\n@keyframes hover-animation {\n  from {\n    width: 0;\n  }\n  to {\n    width: 100%;\n  }\n}\n",".first-screen {\n  padding: 182px 0 84px 0;\n  position: fixed;\n  width: 100vw;\n  top: 10px;\n  z-index: -10;\n\n  @media (max-width: 1440px) {\n    padding-top: 10vw;\n  }\n\n  @media (max-width: $max-mobile-width) {\n    padding: 95px 0 125px 0;\n    position: relative;\n    top: -2px;\n  }\n\n  &::before {\n    @include defaul-pseudo-element(100vw, 63.26vw);\n    background: url(\"./../../public/img/first-screen-bg.png\") no-repeat center\n      center;\n    background-size: cover;\n    left: 0;\n    top: -70px;\n    z-index: -2;\n\n    @media (min-resolution: 2dppx) {\n      background-image: url(\"./../../public/img/first-screen-bg@2x.png\");\n    }\n\n    @media (max-width: $max-mobile-width) {\n      height: 812px;\n      background-image: url(\"./../../public/img/first-screen-bg--mobile.png\");\n      background-size: cover;\n\n      @media (min-resolution: 2dppx) {\n        background-image: url(\"./../../public/img/first-screen-bg--mobile@2x.png\");\n      }\n    }\n  }\n}\n\n.first-screen__title {\n  font-weight: 400;\n  font-size: 16px;\n  line-height: 120%;\n\n  text-transform: uppercase;\n  color: #f8ab0e;\n\n  @include margin-bottom(16px);\n\n  @media (max-width: $max-mobile-width) {\n    font-size: 14px;\n  }\n}\n\n.first-screen__text {\n  font-family: \"Titan One\", sans-serif;\n  font-style: normal;\n  font-weight: 400;\n  font-size: 55px;\n  line-height: 125%;\n\n  text-transform: uppercase;\n  color: #ffffff;\n  max-width: 635px;\n\n  @include margin-bottom(32px);\n\n  @media (max-width: $max-mobile-width) {\n    max-width: none;\n    font-size: 45px;\n  }\n}\n\n.first-screen__buttons-container {\n  margin-bottom: 240px;\n\n  @media (max-width: 1440px) {\n    margin-bottom: 10.5vw;\n  }\n\n  @media (max-width: $max-mobile-width) {\n    display: grid;\n    grid-template-columns: repeat(2, 1fr);\n    gap: 2.3%;\n\n    margin-bottom: 370px;\n  }\n}\n\n.first-screen__button {\n  padding: 16px 48px;\n\n  &:first-child {\n    margin-right: 16px;\n  }\n\n  @media (max-width: $max-mobile-width) {\n    padding: 16px 0;\n    margin: 0;\n    width: 100%;\n  }\n}\n",".crypto-exchanges__list {\n  @include list-none();\n  display: flex;\n  flex-wrap: wrap;\n  max-width: 864px;\n  justify-content: space-around;\n  margin: 0 auto;\n}\n\n.crypto-exchanges__item {\n  &:not(:last-child) {\n    margin-bottom: 20px;\n  }\n}\n",".our-uniqueness {\n  padding-top: 139px;\n  display: grid;\n  gap: 32px;\n  grid-template-columns: 1fr 1fr;\n\n  @media (max-width: $max-mobile-width) {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    gap: 0;\n    padding-top: 0;\n  }\n\n  @media (min-width: $max-mobile-width) {\n    align-items: center;\n    padding-bottom: 263px;\n  }\n}\n\n.our-uniqueness__title {\n  @include margin-bottom(24px);\n}\n\n.our-uniqueness__img {\n  display: block;\n  margin: 0 auto;\n}\n",".our-benefits__list {\n  @include list-none();\n  width: 100%;\n  display: flex;\n  justify-content: sp